'use client'

import { useState } from 'react'

const meals = [
  { title: 'Pizza', ingredients: ['Dough', 'Tomato Sauce', 'Mozzarella', 'Pepperoni', 'Basil'] },
  { title: 'Tacos', ingredients: ['Tortillas', 'Ground Beef', 'Lettuce', 'Cheddar', 'Salsa'] },
  { title: 'Pasta', ingredients: ['Spaghetti', 'Marinara Sauce', 'Meatballs', 'Parmesan'] },
  { title: 'Salad', ingredients: ['Romaine Lettuce', 'Cherry Tomatoes', 'Cucumber', 'Croutons', 'Ranch Dressing'] },
  { title: 'Stir Fry', ingredients: ['Chicken Breast', 'Broccoli', 'Carrots', 'Soy Sauce', 'Rice'] },
]

export default function GroceryListPage() {
  const [selectedIngredients, setSelectedIngredients] = useState<string[]>([])
  const [expandedMeal, setExpandedMeal] = useState<string | null>(null)

  const toggleIngredient = (ingredient: string) => {
    if (selectedIngredients.includes(ingredient)) {
      setSelectedIngredients(selectedIngredients.filter(item => item !== ingredient))
    } else {
      setSelectedIngredients([...selectedIngredients, ingredient])
    }
  }

  const handleAddToInstacart = () => {
    if (selectedIngredients.length === 0) return alert('Please select some ingredients!')
    const searchQuery = selectedIngredients.map(item => encodeURIComponent(item)).join('%20')
    const instacartURL = `https://www.instacart.com/store/search_v3/${searchQuery}`
    window.open(instacartURL, '_blank')
  }

  return (
    <div className="p-8">
      <h1 className="text-3xl font-bold mb-6">üõí Grocery List</h1>
      {meals.map((meal) => (
        <div key={meal.title} className="mb-6 p-4 bg-gray-100 rounded-lg shadow-md">
          <div className="flex justify-between items-center">
            <h2 className="text-xl font-semibold">{meal.title}</h2>
            <button
              className="text-blue-500 hover:underline"
              onClick={() => setExpandedMeal(expandedMeal === meal.title ? null : meal.title)}
            >
              {expandedMeal === meal.title ? 'Hide Ingredients' : 'Show Ingredients'}
            </button>
          </div>

          {expandedMeal === meal.title && (
            <div className="mt-4 space-y-2">
              {meal.ingredients.map((ingredient) => (
                <div key={ingredient} className="flex items-center">
                  <input
                    type="checkbox"
                    id={ingredient}
                    checked={selectedIngredients.includes(ingredient)}
                    onChange={() => toggleIngredient(ingredient)}
                    className="mr-2"
                  />
                  <label htmlFor={ingredient}>{ingredient}</label>
                </div>
              ))}
            </div>
          )}
        </div>
      ))}

      <button
        onClick={handleAddToInstacart}
        className="mt-6 w-full bg-green-500 hover:bg-green-600 text-white py-3 rounded-xl text-xl font-semibold"
      >
        Add Selected to Instacart üõçÔ∏è
      </button>
    </div>
  )
}
